import vm from '@/main'
import { getUser,getUserPro } from "@/api/system";
const user = {
  namespaced: true,
  state: {
    userInfo: {}
  },
  mutations: {
    logout (state) {
      // 清理账号数据
      this.state.permission.permissionList = []
      this.state.permission.routesTree = []
      state.userInfo = null
      localStorage.clear()
      sessionStorage.clear()
    },
    setUserInfo (state, info) {
      vm.$ls.set('userInfo',JSON.stringify(info))
      state.userInfo = JSON.parse(JSON.stringify(info))
    }
  },
  actions: {
    // 获取用户信息
    GetInfo({ commit }) {
      return new Promise((resolve,reject) => {
        getUser().then(res => {
          getUserPro({ userId: res.data.userId }).then(res => {
            commit('setUserInfo', res.data)
            resolve(JSON.parse(JSON.stringify(res)))
          })
        }).catch((err) => {
          reject(err)
        })
      })
    }
  }
}

export default user
