/**
 * 配置异步路由
 * 通过后端获取的权限来配置
 * 
 * 注意：必须配置code来与后端的权限code来匹配权限
 * 注意：不需要权限来控制的路由则只需不添加code属性
 * 
 * 注意：为避免不必要的错误，每个路由需设置name属性！！！
 * 注意：为避免不必要的错误，每个路由需设置name属性！！！
 * 注意：为避免不必要的错误，每个路由需设置name属性！！！
 */

export default  [
  {//路由出口在main.js
    path: '/main',
    name: 'mainHome',
    // code:'cscp.app.mainHome',
    component: () => import('../views/main/index.vue'),
    children: [
      {
        path: '/home',
        name: 'home',
        // code:'cscp.app.home',
        meta: {
          title: '智慧大院平台-首页'
        },
        component: () =>import ( '../views/main/home.vue' )
      },
      {
        path: '/personalCenter',
        name: 'personalCenter',
        meta: {
          title: '我的'
        },
        component: () =>import ( '../views/main/personalCenter.vue' )
      }
    ]
  },
  {
    //集成的子系统的路由
    path: '/subsystem',
    name: 'subsystem',
    component: { render: (e) => e("router-view") },
    children: [
      {
        //智慧食堂页面
        path: '/wisdomCanteen/wisdomCanteenHome',
        name: 'wisdomCanteenHome',
        // code:'cscp.app.wisdomCanteenHome',
        meta: {
          title: '智慧大院平台-智慧食堂',
        },
        component: () => import('../views/subsystem/wisdomCanteen.vue')
      },
      {
        //智慧食堂消费码页面
        path: '/wisdomCanteen/consumerCode',
        name: 'consumerCode',
        props: {
          redirect_uri:'&redirect_uri=showQRCode'
        },
        // code:'cscp.app.consumerCode',
        meta: {
          title: '智慧大院平台-消费码'
        },
        component: () => import('../views/subsystem/wisdomCanteen.vue')
      },
    ]
  },
  {
    path: '*',
    redirect: '/404',
  }
]