/* eslint-disable no-undef */

/** 封装的超长加解密方法
 * param {encryptData}  数据加密
 * export {decryptData}  数据解密
 * const {rsaPubKey} fun 加密公钥
 * const {rsaPriKey} fun 解密私钥
 */

const rsaPriKey = ''
const rsaPubKey = 'MIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQCASj2TglFfEwq+4l09sVvqWMP9f41W+XAmnadEX1zemJ48Rh1YBTHUvMOSIXbm9cquwCgSli4WzSYlLzrXnvWPU7yIZPqHSIflh2HGCiyUWLkxlg0mUTAu656C/yDMSyRI+tZPd29Ks9D/yggX/jlS3aF3umkMd3/+aDup7orFzQIDAQAB'

export const encryptData = function (str) {
  if (rsaPubKey) {
    // 对数据进行加密
    let encryptor = new JSEncrypt() // 新建JSEncrypt对象
    encryptor.setPublicKey(rsaPubKey) // 设置公钥
    return encryptor.encryptLong(str)
  } else {
    return str
  }
}
export const decryptData = function (str) {
  if (rsaPriKey) {
    // 对数据进行解密
    let encryptor = new JSEncrypt() // 新建JSEncrypt对象
    encryptor.setPrivateKey(rsaPriKey) // 设置私钥
    return encryptor.decryptLong(str)
  } else {
    return str
  }
}
// 加密方法
JSEncrypt.prototype.encryptLong = function (str) {
  try {
    var ct = ''
    // RSA每次加密117bytes，需要辅助方法判断字符串截取位置
    // 1.获取字符串截取点
    var bytes = []
    bytes.push(0)
    var byteNo = 0
    var len, c
    len = str.length
    for (var i = 0; i < len; i++) {
      c = str.charCodeAt(i)
      if (c >= 0x010000 && c <= 0x10ffff) {
        byteNo += 4
      } else if (c >= 0x000800 && c <= 0x00ffff) {
        byteNo += 3
      } else if (c >= 0x000080 && c <= 0x0007ff) {
        byteNo += 2
      } else {
        byteNo += 1
      }
      if (byteNo % 117 >= 114 || byteNo % 117 === 0) {
        bytes.push(i)
        byteNo = 0
      }
    }
    // 2.截取字符串并分段加密
    if (bytes.length > 1) {
      for (var j = 0; j < bytes.length - 1; j++) {
        var subStr
        if (j === 0) {
          subStr = str.substring(0, bytes[j + 1] + 1)
        } else {
          subStr = str.substring(bytes[j] + 1, bytes[j + 1] + 1)
        }
        var t1 = this.encrypt(subStr)
        ct += t1
      }
      if (bytes[bytes.length - 1] !== str.length - 1) {
        var lastStr = str.substring(bytes[bytes.length - 1] + 1)
        ct += this.encrypt(lastStr)
      }
      return ct
    }
    return this.encrypt(str)
  } catch (ex) {
    return false
  }
}
// 解密方法
JSEncrypt.prototype.decryptLong = function (string) {
  try {
    // 加密的数组
    const arr = string.split('=')
    const length = arr.length
    let str = ''
    arr.forEach((item, i) => {
      if (i !== length - 1) {
        // 最后一条数据为null，则需跳过
        str += this.decrypt(item)
      }
    })
    return str
  } catch (ex) {
    return false
  }
}
