
import axios from 'axios'
import vm from '@/main'
import Exif from 'exif-js'
import { ACCESS_TOKEN } from '@/config/defaultSettings'
import { encryptData} from './jsencrypt' //rsa加密方法，可超长

const util = {}

// read env from configs file
// const env = process.env.NODE_ENV// 判断环境： development---->开发环境  production---->生产环境
// util.baseUrl = 'http://192.168.136.73:8088'

// util.baseUrl = 'http://172.30.199.129:8088'// 本系统测试服ip
util.baseUrl = 'http://133.0.214.12:8088'// 本系统正式服ip

// util.WISDOM_CANTEEN_IP = 'http://172.30.199.132' // 智慧食堂测试ip
util.WISDOM_CANTEEN_IP = 'http://133.0.214.14' // 智慧食堂生产环境ip



//免登录访问本系统时，需根据本系统部署的位置更改根目录 vue.config.js 中的 target:IP,这是为了解决跨域问题

util.http = axios.create({
  baseURL: util.baseUrl,
  timeout: 120000,
  headers: {
    'Content-Type':'application/json;charset=utf-8'
  }
})

// http请求拦截
util.http.interceptors.request.use(
  function (config) {
    // 打印传输的数据，方便开发
    if (process.env.NODE_ENV === 'development') {
      console.log('\n\n')
      console.log('%c' + config.url, 'color:#FF87CA;font-size:20px;font-weight: 900;')
      console.log('config.data', config.data)
      console.log('%c' + config.method, 'color:#FE7E6D;font-size:20px;font-weight: 900;')
    }
    // 加密data的数据
    if (config.data) config.data = encryptData(JSON.stringify(config.data))
    // 设置token
    const token = vm.$ls.get(ACCESS_TOKEN)
    if (token) {
      config.headers[ACCESS_TOKEN] = token // 让每个请求携带自定义 token
    }
    return config
  },
  function (error) {
    // 请求失败的处理
    return Promise.reject(error)
  }
)
// http响应拦截
util.http.interceptors.response.use(
  function (res) {
    const token = res.config.headers[ACCESS_TOKEN]
    if (token) {
      vm.$ls.set(ACCESS_TOKEN, token, 0.5 * 24 * 60 * 60 * 1000)
    }
    return res
  },
  function (error) {
    // Do something with response error
    if (error.response) {
      switch (error.response.status) {
        case 401:
          if (vm.$route.name !== 'login') {
            vm.$notify({
              type: 'warning',
              message: '身份认证信息已失效，请重新登录。',
              duration: 2000
            })
            vm.$store.commit('user/logout')
            setTimeout(() => {
              vm.$router.push('/login')
            }, 1000)
          } else {
            return Promise.reject(error)
          }
          break
        case 400:
        case 404:
        case 500:
          vm.$notify({
            message:
              error.response.data.message ||
              `${error.response.status}：未知错误`,
            duration: 3000
          })
          return Promise.reject(error)
        case 403:
          vm.$notify({
            type: 'warning',
            message:
              error.response.data.message ||
              `${error.response.status}：未知错误`,
            duration: 3000
          })
          break
        default:
          return Promise.reject(error)
      }
    } else if (error.message) {
      if (error.message === 'Network Error') {
        vm.$notify('网络连接错误，请检查网络环境！')
      } else if (error.message.indexOf('timeout') >= 0) {
        vm.$notify('网络连接超时，请重试！')
      } else {
        vm.$notify(error.message)
      }
    } else {
      vm.$notify('知识盲区的错误，联系程序员小哥哥吧！')
    }
  }
)

/**ios图片旋转问题
 * param {File} file 图片文件
 * 返回一个修改后的bse64文件
 */
util.iosRotateImg = function (file) {
  return new Promise((resolve) => {
    let Orientation = ''
    //去获取拍照时的信息，解决拍出来的照片旋转问题
    Exif.getData(file, function () {
      Orientation = Exif.getTag(this, 'Orientation')
    })
    // 看支持不支持FileReader
    if (!file || !window.FileReader) return
    if (/^image/.test(file.type)) {
      // 创建一个reader
      let reader = new FileReader()
      // 将图片2将转成 base64 格式
      reader.readAsDataURL(file)
      // 读取成功后的回调
      reader.onloadend = function () {
        let result = this.result
        let img = new Image()
        img.src = result
        img.onload = () => {
          resolve(compress(img, Orientation))
        }
      }
    }
  })
  // 压缩图片
  function compress(img, Orientation) {
    let canvas = document.createElement('canvas')
    let ctx = canvas.getContext('2d')
    //瓦片canvas
    let tCanvas = document.createElement('canvas')
    let tctx = tCanvas.getContext('2d')
    // let initSize = img.src.length
    let width = img.width
    let height = img.height
    //如果图片大于四百万像素，计算压缩比并将大小压至400万以下
    let ratio
    if ((ratio = (width * height) / 10000000) > 1) {
      //
      ratio = Math.sqrt(ratio)
      width /= ratio
      height /= ratio
    } else {
      ratio = 1
    }
    canvas.width = width
    canvas.height = height
    //    铺底色
    ctx.fillStyle = '#fff'
    ctx.fillRect(0, 0, canvas.width, canvas.height)
    //如果图片像素大于100万则使用瓦片绘制
    let count
    if ((count = (width * height) / 1000000) > 1) {
      //
      count = ~~(Math.sqrt(count) + 1) //计算要分成多少块瓦片
      //      计算每块瓦片的宽和高
      let nw = ~~(width / count)
      let nh = ~~(height / count)
      tCanvas.width = nw
      tCanvas.height = nh
      for (let i = 0; i < count; i++) {
        for (let j = 0; j < count; j++) {
          tctx.drawImage(
            img,
            i * nw * ratio,
            j * nh * ratio,
            nw * ratio,
            nh * ratio,
            0,
            0,
            nw,
            nh
          )
          ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh)
        }
      }
    } else {
      ctx.drawImage(img, 0, 0, width, height)
    }
    //修复ios上传图片的时候 被旋转的问题
    if (Orientation != '' && Orientation != 1) {
      switch (Orientation) {
        case 6: //需要顺时针（向左）90度旋转
          //
          // rotateImg(img, 'right', canvas)
          break
        case 8: //需要逆时针（向右）90度旋转
          rotateImg(img, 'left', canvas)
          break
        case 3: //需要180度旋转
          rotateImg(img, 'turntwo', canvas)
          break
      }
    }
    //进行最小压缩
    let ndata = canvas.toDataURL('image/jpeg', 0.1)
    tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0
    return ndata
  }
  //旋转图片
  function rotateImg(img, direction, canvas) {
    //alert(img)
    //最小与最大旋转方向，图片旋转4次后回到原方向
    var min_step = 0
    var max_step = 3
    //var img = document.getElementById(pid)
    if (img == null) return
    //img的高度和宽度不能在img元素隐藏后获取，否则会出错
    var height = img.height
    var width = img.width
    //var step = img.getAttribute('step')
    var step = 2
    if (step == null) {
      step = min_step
    }
    if (direction == 'right') {
      step++
      //旋转到原位置，即超过最大值
      step > max_step && (step = min_step)
    } else {
      step--
      step < min_step && (step = max_step)
    }
    //旋转角度以弧度值为参数
    var degree = (step * 90 * Math.PI) / 180
    var ctx = canvas.getContext('2d')
    switch (step) {
      case 0:
        canvas.width = width
        canvas.height = height
        ctx.drawImage(img, 0, 0)
        break
      case 1:
        canvas.width = height
        canvas.height = width
        ctx.rotate(degree)
        ctx.drawImage(img, 0, -height)
        break
      case 2:
        canvas.width = width
        canvas.height = height
        ctx.rotate(degree)
        ctx.drawImage(img, -width, -height)
        break
      case 3:
        canvas.width = height
        canvas.height = width
        ctx.rotate(degree)
        ctx.drawImage(img, -width, 0)
        break
    }
  }
}

/**时间转换
 * param {Time} fmt 时间格式
 * 返回格式化后的时间
 */
util.format = function (date, fmt) {
  let o = {
    'M+': date.getMonth() + 1, // 月份
    'd+': date.getDate(), // 日
    // 'h+': date.getHours() % 12 === 0 ? 12 : date.getHours() % 12, // 小时
    'h+': date.getHours(), // 小时
    'H+': date.getHours(), // 小时
    'm+': date.getMinutes(), // 分
    's+': date.getSeconds(), // 秒
    'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
    S: date.getMilliseconds() // 毫秒
  }
  let week = {
    0: '\u65e5',
    1: '\u4e00',
    2: '\u4e8c',
    3: '\u4e09',
    4: '\u56db',
    5: '\u4e94',
    6: '\u516d'
  }
  let time = {
    0: '\u51cc\u6668', // 上午
    1: '\u4e0a\u5348', // 下午
    2: '\u4e0b\u5348', // 晚上
    3: '\u665a\u4e0a' // 凌晨
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + '').substr(4 - RegExp.$1.length)
    )
  }
  if (/(E+)/.test(fmt)) {
    fmt = fmt.replace(
      RegExp.$1,
      (RegExp.$1.length > 1
        ? RegExp.$1.length > 2
          ? '/u661f/u671f'
          : '/u5468'
        : '') + week[date.getDay() + '']
    )
  }
  if (/(T)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, time[parseInt(date.getHours() / 6)])
  }
  for (let k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length)
      )
    }
  }
  return unescape(fmt.replace(/\/u/g, '%u'))
}

export default util