import util from '@/libs/util.js'
// 免密登录接口
export function ssoLogin (params) {
  return util.http({
    baseURL:'',
    url: '/ssoLogin',
    method: 'get',
    params
  })
}
// 获取验证码
export function getDigitalCaptcha () {
  return util.http({
    url: '/api/catpcha/digitalCaptcha',
    method: 'get'
  })
}
// 登录
export function login (data,headers) {
  return util.http({
    url: '/api/system/login',
    method: 'post',
    headers,
    data
  })
}
// 修改密码
export function updatePswd (data,headers) {
  return util.http({
    url: '/api/system/cscpUserPassword',
    method: 'put',
    headers,
    data
  })
}