import util from '@/libs/util.js'

// 获取路由
export function getRouter () {
  return util.http({
    url: '/api/system/cscpMenus',
    method: 'get'
  })
}
// 获取用户，比翼自带
export function getUser () {
  return util.http({
    url: '/api/system/cscpCurrentUserDetails',
    method: 'get'
  })
}
// 获取用户，新增获取的信息
export function getUserPro (params) {
  return util.http({
    url: '/api/cscpUserDetailCustoms/findOne',
    method: 'get',
    params
  })
}
// 获取字典
export function getDictType (parameter) {
  return util.http({
    url: 'api/CscpDic/getDicByDicType',
    method: 'post',
    data: parameter
  })
}