
import { getRouter } from "@/api/system";
import asyncRoutes from "@/router/asyncRoutes";

function filterAsyncRouter(routerMap, tree) {//筛选权限
  return routerMap.filter(route => {
    //没有route.code则表示无需权限控制
    if (!route.code || tree.some(item => item.permissionCode === route.code)) {
      if (route.children && route.children.length) {
        route.children = filterAsyncRouter(route.children, tree)
      }
      return true
    }
    return false
  })
}
const app = {
  namespaced: true,
  state: {
    routesTree: [], // 路由树数据
    permissionList: [], // 路由原始list数据
  },
  mutations: {
    // 保存路由数据原始的关系树
    setRoutes (state, data) {
      state.routesTree = data.routesTree
      state.permissionList =  data.permissionList
    },
  },
  actions: {
    // 获取路由信息
    GetRouterTree({ commit }) {
      return new Promise((resolve) => {
        getRouter().then(res => {
          const routesTree = filterAsyncRouter(asyncRoutes,res.data)
          commit('setRoutes',{routesTree,permissionList:res.data})
          resolve(routesTree)
        })
        // 无权限控制
        .catch(() => {
          const data =  []
          const routesTree = filterAsyncRouter(asyncRoutes,data)
          commit('setRoutes',{routesTree,permissionList:data})
          resolve(routesTree)
        })
        //// 有权限控制
        // .catch((err) => {
        //   reject(err.response)
        // })
      })
    },
  }
}

export default app
