
import Vue from 'vue'
import vm from '@/main'
import router from '@/router'
import store from '@/store/store'
import { ACCESS_TOKEN } from '@/config/defaultSettings'

import { ssoLogin } from "@/api/login"//免密码登录接口

import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css'
NProgress.configure({ showSpinner: false }) // NProgress Configuration

// 控制路由页面的权限
const whiteList = ['login','updatePswd','passCode','passCertificate','passCertificateDaily','passCertificateTemporary','404','accessDenied'] //路由白名单
router.beforeEach((to, from, next) => {
  NProgress.start() // start progress bar
  if (to.meta.title) {// 路由发生变化修改页面title 
    document.title = to.meta.title
  }
  if (Vue.ls.get(ACCESS_TOKEN)) {
    if (!store.state.permission.routesTree.length) {//通过vuex存的异步获取的路由来判断是否添加路由
        store.dispatch('user/GetInfo')
        store.dispatch('permission/GetRouterTree').then(res => {
          res.forEach(item => {
              router.addRoute(item)//动态添加路由
          });
          if (to.path === '/login') {
            next({ name: 'home' })
          } else {
            next()
          }
        }).catch((err) => {
          console.log(err);
          vm.$notify({ message: '用户权限验证失败，请重新登录。', duration: 2000 })
          next({ path: '/login' })
        })
    } else {
      next()
    }
  } else if (to.query.code) {
    ssoLogin({ code: to.query.code }).then(res => { // 通过获取query的token设置免登录
      if (res && res.status === 200) {
        const headers = res.data.headers
        Vue.ls.set(
          //本系统需要的token
          ACCESS_TOKEN,
          headers[ACCESS_TOKEN][0],
          0.5 * 24 * 60 * 60 * 1000
        )
        Vue.ls.set(
          //子系统免登录token
          'access_token',
          headers['Access-Token'][0],
          0.5 * 24 * 60 * 60 * 1000
        )
        next({ path: to.path })
      }
    }).catch(err => {
      console.log('err: ', err.response);
      vm.$notify({ type: 'warning', message: '用户权限验证失败。',duration:2000 })
      next({ path: '/accessDenied' })
    })
  } else {
    if (whiteList.includes(to.name)) {
      // 在免登录白名单，直接进入
      next()
    } else {
      next({ path: '/login' })
    }
  }
  next()
})

router.afterEach(() => {
  NProgress.done() // finish progress bar
})
